.container {
  width: 409px;
  height: 126px;
  border: 1px solid rgb(216, 208, 165);
  margin: 0 auto;
  text-align: center;
  margin-bottom: 20px;
}

.title {
  color: rgb(216, 208, 165);
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
}

.list_item {
  border: 1px solid white;
  background-color: rgb(216, 208, 165);
  color: white;
  padding: 9px;
}
